.span {
    visibility: visible;
    --word-total: 1;
    --char-total: 9;
    color: #fff;
    line-height: 1.4;
    font-family: inherit;
    display: inline-block;
    font-size: 80px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 4px;
    text-shadow: 0 8px rgb(255 255 255 / 5%);
}
.little{
    visibility: visible;
    animation-delay: 0.5s;
    animation-name: fadeIn;
    color: #dc965a;
    font-weight: 300;
    font-size: 17px;
    text-transform: uppercase;
    letter-spacing: 14px;
    margin-bottom: 5px;
    line-height: 1.4;
    font-family: inherit;
}